import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'hn-search',
  exportAs: 'hnSearch',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './hn-search.component.html'
})
export class HnSearchComponent implements OnInit {
  @Input() formList: any = [];

  @Input() keyword = 'searchContent';

  @Input() placeholder = 'Ã¨Â¯Â·Ã¨Â¾ÂÃ¥ÂÂ¥Ã¦ÂÂ¥Ã¨Â¯Â¢Ã¦ÂÂ¡Ã¤Â»Â¶';

  @Input() render: any;

  @Output() search: EventEmitter<any> = new EventEmitter();

  modeVisible: any = false;

  form: any = {};

  resetForm: any = [];

  validateForm: FormGroup;

  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.form[this.keyword] = null;
    this.resetForm = JSON.parse(JSON.stringify(this.formList));
    this.initForm();
  }

  /**
   * Ã¥ÂÂÃ¥Â§ÂÃ¥ÂÂÃ¨Â¡Â¨Ã¥ÂÂ
   */
  initForm() {
    const form: any = this.setInitForm(this.formList);
    this.validateForm = this.fb.group({ ...form });
  }

  /**
   * Ã¨Â®Â¾Ã§Â½Â®Ã¥ÂÂÃ¥Â§ÂÃ¥ÂÂÃ¨Â¡Â¨Ã¥ÂÂÃ¦ÂÂ°Ã¦ÂÂ®
   * @param arr Ã¦ÂÂ°Ã¦ÂÂ®
   */
  setInitForm(arr: any) {
    let form: any = {};
    arr.forEach(item => {
      if (item.children) {
        form = { ...form, ...this.setInitForm(item.children) };
      } else {
        if (item.type === 'checkbox') {
          form[item.key] = [Object.assign([], item.options)];
        } else {
          form[item.key] = [null];
        }
      }
    });
    return form;
  }

  /**
   * Ã¨Â¿ÂÃ¦Â»Â¤Ã¦ÂÂ°Ã§Â»Â
   * @param data Ã¥ÂÂ¼Ã¯Â¼ÂÃ¦ÂÂ°Ã§Â»Â
   */
  filterArr(data: any[]) {
    return data
      .filter(item => {
        return item.checked;
      })
      .map(item => {
        return item.value;
      });
  }

  /**
   * Ã¥ÂÂÃ¦ÂÂ¢Ã©Â«ÂÃ§ÂºÂ§Ã¦ÂÂÃ§Â´Â¢
   */
  handleToggle(): void {
    this.modeVisible = !this.modeVisible;
  }

  /**
   * Ã¥ÂÂÃ¦Â¶ÂÃ¦ÂÂÃ¤Â½Â
   */
  handleCancle(): void {
    this.modeVisible = false;
  }

  /**
   * Ã©ÂÂÃ§Â½Â®Ã¨Â¡Â¨Ã¥ÂÂ
   */
  handleReset(): void {
    this.formList = JSON.parse(JSON.stringify([...this.resetForm]));
    this.initForm();
    this.search.emit({ ...this.form });
  }

  /**
   * Ã§Â¡Â®Ã¨Â®Â¤Ã¦ÂÂÃ¤Â½Â
   */
  handleConfirm(): void {
    this.submitForm();
  }

  /**
   * Ã©ÂÂ®Ã§ÂÂenterÃ¤ÂºÂÃ¤Â»Â¶
   */
  handleKeyupEnter(): void {
    this.submitForm();
  }

  /**
   * Ã¦ÂÂÃ¤ÂºÂ¤Ã¨Â¡Â¨Ã¥ÂÂ
   */
  submitForm(): void {
    const value = this.validateForm.value;
    const params: any = { ...this.form, ...this.setParams(this.formList, value) };
    this.search.emit(params);
  }

  /**
   * Ã¨Â®Â¾Ã§Â½Â®data
   * @param data Ã¦ÂÂ°Ã¦ÂÂ®
   */
  setParams(arr: any, data: any) {
    let params = {};
    arr.forEach(item => {
      if (item.children) {
        params = { ...params, ...this.setParams(item.children, data) };
      } else {
        if (item.type === 'checkbox') {
          const str = this.filterArr(data[item.key]).join(',') || null;
          params[item.key] = str;
        } else {
          params[item.key] = data[item.key];
        }
      }
    });
    return params;
  }
}
