import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  TemplateRef,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
  // ...
} from '@angular/animations';

@Component({
  selector: 'hn-scroll-list',
  exportAs: 'hnScrollList',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './hn-scroll-list.component.html',
  animations: []
})
export class HnScrollListComponent implements OnInit {
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã¦ÂÂ°Ã¦ÂÂ®
  @Input() data: any[] = [];
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã©Â¡Â¹Ã¦ÂÂ°Ã©ÂÂ
  @Input() count = 6;
  // Ã¤Â¸ÂÃ¦Â¬Â¡Ã¦Â»ÂÃ¥ÂÂ¨Ã¤Â¸ÂªÃ¦ÂÂ°
  @Input() scrollCount = 1;
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã©Â¡Â¹Ã©Â«ÂÃ¥ÂºÂ¦
  @Input() itemHeight = 60;
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã¥ÂÂ¨Ã§ÂÂ»Ã©ÂÂÃ¥ÂºÂ¦
  @Input() speed = 300;
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã¥Â»Â¶Ã¨Â¿Â
  @Input() delay = 2000;

  @Input() renderItem: TemplateRef<void>;
  // Ã¦Â»ÂÃ¥ÂÂ¨Ã©Â¡Â¹clickÃ¤ÂºÂÃ¤Â»Â¶
  @Output() click: EventEmitter<any> = new EventEmitter();

  y = 0;
  curr = 0;
  list: any = [];
  nextList: any = [];
  totalList: any = [];
  inAnimation = false;
  inEnter = false;

  initContentStyle = {
    transition: '',
    marginTop: '0px'
  };
  contentStyle = {};

  contentHeight: any;
  scrollHeight: any;
  listStyle: any;
  itemStyle: any;

  timer: any = null;

  constructor() {}

  ngOnInit() {
    this.initData();
    this.reset();
  }

  initData() {
    this.contentHeight = this.itemHeight * this.count;
    this.scrollHeight = this.scrollCount * this.itemHeight;
    this.listStyle = {
      height: this.contentHeight + 'px'
    };
    this.itemStyle = {
      height: this.itemHeight + 'px'
    };
  }

  /**
   * Ã©ÂÂÃ§Â½Â®Ã¦Â»ÂÃ¥ÂÂ¨
   */
  reset() {
    this.clearScroll();
    this.y = 0;
    this.curr = 0;
    this.inAnimation = false;
    this.contentStyle = { ...this.initContentStyle };
    this.loop();
  }

  /**
   * Ã©Â¼Â Ã¦Â ÂoverÃ¦ÂÂÃ¤Â½Â
   */
  handleMouseover() {
    this.inEnter = true;
    this.clearScroll();
  }

  /**
   * Ã©Â¼Â Ã¦Â ÂÃ§Â§Â»Ã¥ÂÂºÃ¦ÂÂÃ¤Â½Â
   */
  handleMouseout() {
    this.inEnter = false;
    this.loop();
  }

  /**
   * Ã¥ÂÂ¨Ã§ÂÂ»Ã§Â»ÂÃ¦ÂÂ
   */
  handleEnd() {
    this.inAnimation = false;
    this.curr = 0;
    this.contentStyle = { ...this.initContentStyle };
    this.loop();
  }

  /**
   * Ã¦Â»ÂÃ¥ÂÂ¨Ã¥ÂÂ¨Ã§ÂÂ»
   */
  loop() {
    if (this.inEnter) {
      return;
    }
    this.getList();
    this.clearScroll();
    if (this.data.length >= this.count) {
      this.getNextList();
      this.totalList = [...this.list, ...this.nextList];

      this.timer = setTimeout(() => {
        this.inAnimation = true;
        this.y += this.scrollCount;
        this.curr += this.scrollCount;
        this.contentStyle = {
          transition: this.inAnimation ? this.speed + 'ms' : '',
          marginTop: -(this.curr * this.itemHeight) + 'px'
        };
      }, this.delay);
    }
  }

  /**
   * Ã¦Â¸ÂÃ©ÂÂ¤Ã¦Â»ÂÃ¥ÂÂ¨
   */
  clearScroll() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  /**
   * Ã¨ÂÂ·Ã¥ÂÂÃ¥ÂÂÃ¨Â¡Â¨
   */
  getList() {
    const { y, count, data } = this;
    const curr = y % data.length;
    let list = data.slice(curr, curr + count);
    // Ã¨Â¡Â¥Ã¤Â¸ÂÃ¨Â¶Â³
    if (list.length < count && this.data.length >= count) {
      list = [...list, ...data.slice(0, count - list.length)];
    }
    this.list = list;
  }

  /**
   * Ã¨ÂÂ·Ã¥ÂÂÃ¤Â¸ÂÃ©ÂÂ¨Ã¥ÂÂÃ¥ÂÂÃ¨Â¡Â¨
   */
  getNextList() {
    const { y, count, scrollCount, data } = this;
    const curr = (y + count) % data.length;
    let nextList = data.slice(curr, curr + scrollCount);
    // Ã¨Â¡Â¥Ã¤Â¸ÂÃ¨Â¶Â³
    if (nextList.length < scrollCount) {
      nextList = [...nextList, ...data.slice(0, scrollCount - nextList.length)];
    }
    this.nextList = nextList;
  }

  /**
   * Ã§ÂÂ¹Ã¥ÂÂ»Ã¦Â»ÂÃ¥ÂÂ¨Ã©Â¡Â¹
   * @param row Ã¦Â»ÂÃ¥ÂÂ¨Ã©Â¡Â¹Ã¦ÂÂ°Ã¦ÂÂ®
   */
  handleClick(row: any) {
    this.click.emit(row);
  }
}
