import { FocusMonitor } from '@angular/cdk/a11y';
import {
  forwardRef,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewEncapsulation,
  ChangeDetectionStrategy
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

import { InputBoolean } from 'ng-zorro-antd/core';

@Component({
  selector: 'hn-checkbox-group',
  templateUrl: './hn-checkbox-group.component.html',
  exportAs: 'hnCheckboxGroup',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => HnCheckboxGroupComponent),
      multi: true
    }
  ]
})
export class HnCheckboxGroupComponent implements ControlValueAccessor, OnInit, OnDestroy {
  @Input() options: any[] = [];

  @Input() @InputBoolean() disabled = false;

  @Input() @InputBoolean() checkAll = false;

  @Input() checkAllLabel = 'Ã¥ÂÂ¨Ã©ÂÂ';

  data: any[] = [];

  allChecked = false;

  indeterminate = false;

  constructor(
    private elementRef: ElementRef,
    private focusMonitor: FocusMonitor,
    private cdr: ChangeDetectorRef,
    renderer: Renderer2
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-checkbox-group');
  }

  ngOnInit(): void {
    this.focusMonitor.monitor(this.elementRef, true).subscribe(focusOrigin => {
      if (!focusOrigin) {
        Promise.resolve().then(() => this.onTouched());
      }
    });
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  /**
   * changeÃ¦ÂÂ¹Ã¦Â³Â
   */
  onChange: (value: any) => void = () => null;

  /**
   * touchÃ¦ÂÂ¹Ã¦Â³Â
   */
  onTouched: () => any = () => null;

  /**
   * Ã§Â´Â¢Ã¥Â¼Â
   * @param _index Ã§Â´Â¢Ã¥Â¼ÂÃ¥ÂÂ¼
   * @param option Ã¥Â­ÂÃ©Â¡Â¹
   */
  trackByOption(_index: number, option: any): string {
    return option.value;
  }

  /**
   * Ã¥ÂÂÃ¥ÂÂ¥valueÃ¦ÂÂ¹Ã¦Â³Â
   * @param value Ã¥ÂÂ¼
   */
  writeValue(value: any): void {
    if (!value) {
      return;
    }
    this.options.forEach(item => {
      item.checked = value.includes(item.value);
    });
    this.changeAllCheked(value);
    this.data = value;
    this.cdr.markForCheck();
  }

  /**
   * Ã¦Â³Â¨Ã¥ÂÂchangeÃ¦ÂÂ¹Ã¦Â³Â
   * @param fn changeÃ¦ÂÂ¹Ã¦Â³Â
   */
  registerOnChange(fn: (_: any[]) => {}): void {
    this.onChange = fn;
  }

  /**
   * Ã¦Â³Â¨Ã¥ÂÂtouchÃ¤ÂºÂÃ¤Â»Â¶
   * @param fn touchÃ¦ÂÂ¹Ã¦Â³Â
   */
  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

  /**
   * Ã¨Â®Â¾Ã§Â½Â®Ã§Â¦ÂÃ§ÂÂ¨Ã§ÂÂ¶Ã¦ÂÂ
   * @param isDisabled Ã¦ÂÂ¯Ã¥ÂÂ¦Ã§Â¦ÂÃ§ÂÂ¨
   */
  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  /**
   * checkboxÃ©ÂÂÃ¤Â¸Â­Ã¥ÂÂ¼Ã¦ÂÂ¹Ã¥ÂÂÃ¦ÂÂÃ¤Â½Â
   */
  handleChange(): void {
    const data = this.filterData(this.options);

    this.changeAllCheked(data);
    this.onChange(data);
  }

  handleAllChecked(type: any): void {
    this.options.forEach(item => {
      if (!item.disabled) {
        item.checked = type;
      }
    });
    const data = this.filterData(this.options);
    this.onChange(data);
  }

  changeAllCheked(data: any[]): void {
    const arr = this.options.filter(item => {
      return !item.disabled || (item.disabled && item.checked);
    });
    if (data.length === arr.length) {
      this.allChecked = true;
      this.indeterminate = false;
    } else if (data.length === 0) {
      this.allChecked = false;
      this.indeterminate = false;
    } else {
      this.indeterminate = true;
    }
  }

  filterData(data: any[]) {
    return data
      .filter(item => {
        return item.checked;
      })
      .map(item => {
        return item.value;
      });
  }
}
