import {
  Component,
  OnInit,
  OnChanges,
  Input,
  ElementRef,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import { CountUp } from 'countup.js';

@Component({
  selector: 'hn-countup',
  exportAs: 'hnCountup',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <span class="hn-countup"></span>
  `
})
export class HnCountupComponent implements OnInit, OnChanges {
  instance = null;

  // Ã¥Â¼ÂÃ¥Â§ÂÃ¥ÂÂ¼
  @Input() startVal = 0;
  // Ã§Â»ÂÃ¦ÂÂÃ¥ÂÂ¼
  @Input() endVal: number;
  // Ã¦ÂÂÃ§Â»Â­Ã¦ÂÂ¶Ã©ÂÂ´
  @Input() duration = 1000;
  // Ã¥Â°ÂÃ¦ÂÂ°Ã¤Â½Â
  @Input() decimalPlaces = 0;
  // Ã¥Â®ÂÃ¥ÂÂ¨Ã¥Â±ÂÃ§Â¤ÂºÃ¥ÂÂÃ¨Â°Â
  @Output() ready: EventEmitter<any> = new EventEmitter();

  delay: number;

  constructor(private el: ElementRef) {}

  ngOnInit() {
    this.delay = this.duration / 1000;
    this.create();
  }

  ngOnChanges(changes: any) {
    const value = changes.endVal.currentValue;
    if (this.instance) {
      this.instance.update(value);
    }
  }

  ngOnDestory() {
    this.instance = null;
  }

  // Ã§ÂÂÃ¦ÂÂ
  create() {
    if (this.instance) {
      return;
    }
    const dom = this.el.nativeElement.querySelector('.hn-countup');
    const instance = new CountUp(dom, this.endVal, {
      startVal: this.startVal,
      duration: this.delay,
      decimalPlaces: this.decimalPlaces
    });
    if (instance.error) {
      return;
    }
    this.instance = instance;
    if (this.delay < 0) {
      this.ready.emit({ instance, CountUp });
      return;
    }
    setTimeout(() => {
      instance.start(() => {
        this.ready.emit({ instance, CountUp });
      });
    }, this.delay);
  }
}
